.contact-me-container {
  max-width: 800px;
  width: 100%;
  /* padding: 2rem; */
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.sub-page-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  font-weight: 600;
  color: #333;
  padding-top: 2rem;
}

.contact-me-input {
  width: 85%;
  max-width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 1.5rem;
}

textarea {
  resize: vertical;
}

#sendMessage {
  width: 88%;
  max-width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

#sendMessage:hover {
  background-color: #0056b3;
}

#sendMessage.sending {
  background-color: #ff9800;
  cursor: not-allowed;
  transform: scale(1.05);
}

#sendMessage.sent {
  background-color: #4caf50;
  cursor: default;
  transform: scale(1);
}

.contact-me-mainholder {
  background-color: #181818;
  padding-bottom: 5rem;
}

#contactMe {
  box-shadow: 13px 11px 1px 2px rgba(136, 161, 184, 0.2);
  transition: box-shadow 0.5s ease;
  padding-bottom: 2rem;
}

#contactMe:hover {
  box-shadow: 1px 1px 1px 1px rgba(105, 121, 136, 0.5);
}

textarea {
  resize: none;
}


@media (max-width: 768px) {
  #contactMe {
    min-width: 90%;
  }
}